<template>
	<view class="content">
		<view class="warpper">
			<view class="avator">
				<img :src="imageBase64" />
			</view>
		</view>
		<view class="login-form">
			<u--form labelPosition="left" :model="loginForm">
				<u-form-item prop="username">
					<u--input placeholder="请输入用户名" border="surround" prefixIcon="account" v-model="loginForm.username"></u--input>
				</u-form-item>
				<u-form-item prop="password">
					<u--input placeholder="请输入密码" border="surround" prefixIcon="lock" v-model="loginForm.password"></u--input>
				</u-form-item>
			</u--form>
			<button type="primary" @click="tryToLogin">登录</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imageBase64:'data:image/png;base64,',
				loginForm: {
					username: '',
					password: ''
				},
			}
		},
		methods: {
			tryToLogin(){
				if(this.loginForm.username.length < 1||this.loginForm.password.length < 1){
					uni.$u.toast('请输入用户名或密码')
				}else{
					uni.request({
						url: 'http://'+this.$serviceUrl+':8098/userApi/login',
						method: 'POST',
						data: this.loginForm,
						success: (res) => {
							if(res.data == ''){
								uni.$u.toast('登录失败，为找到登录的用户名或密码错误')
							}else{
								uni.setStorage({
									key: 'user',
									data: {
										id: res.data.userid,
										realname: res.data.realname,
										role: res.data.role
									},
									success: ()=>{
										uni.redirectTo({
											url: '/pages/dashboard/index'
										});
									}
								})
							}
						}
					})
				}
			}
		}
	}
</script>

<style>
.content{
	width: 100vw;
	height: 100vh;
	background: transparent url(data:image/jpeg;base64,) no-repeat scroll;
}
.warpper{
	height: 30vh;
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: flex-end;
}
.avator{
	width: 200upx;
	height: 200upx;
	overflow: hidden;
}
.avator image {
	width: 100%;
	height: 100%;
}
.login-form{
	padding: 0 100upx;
	margin-top: 80px;
}
</style>
